import React, { Component } from 'react';
import PropTypes from 'prop-types';
import RemoteVideo from './RemoteVideo';
import * as config from '../../config';

const MAX_REMOTE_VIDEOS = config.CHIME_ROOM_MAX_ATTENDEE;

class RemoteVideoGroup extends Component {

  state = {
    rosterChanged: false,
    roster: [],
    shareVideoElement: React.createRef(),
    isMyContent: false,
    isSharingContent: false,
    sharingTileId: 0,
    sharingInfo: {}
  }

  constructor() {
    super();
    this.previousRoster = {};
  }

  findRosterSlot = (attendeeId) => {
    let index;
    for (index = 0; index < this.state.roster.length; index++) {
      if (this.state.roster[index].attendeeId === attendeeId) {
        return index;
      }
    }
    for (index = 0; index < this.state.roster.length; index++) {
      if (!this.state.roster[index].attendeeId) {
        return index;
      }
    }
    return 0;
  }

  rosterCallback = (newRoster) => {
    if (Object.keys(newRoster).length > 2) {
      if (config.DEBUG) console.log('More than 2');
    }

    if (Object.keys(newRoster).length < Object.keys(this.previousRoster).length) {
      if (config.DEBUG) console.log('Attendee(s) left');
      const differ = Object.keys(this.previousRoster).filter(k => this.previousRoster[k] !== newRoster[k]);
      if (config.DEBUG) console.log(differ);

      if (differ.length) {
        let i;
        for (i in differ) {
          const index = this.findRosterSlot(differ[i]);
          const roster = this.state.roster;
          roster[index] = {
            videoElement: roster[index].videoElement
          }
          this.setState({ roster });
        }
      }
    }

    this.previousRoster = Object.assign({}, newRoster);

    let attendeeId;
    for (attendeeId in newRoster) {

      // Exclude self
      if (attendeeId === this.props.joinInfo.Attendee.AttendeeId) {
        continue;
      }

      // exclude empty name
      if (!newRoster[attendeeId].name) {
        continue;
      }

      const index = this.findRosterSlot(attendeeId);
      const roster = this.state.roster;
      const attendee = {
        ...roster[index],
        attendeeId,
        ...newRoster[attendeeId]
      };

      roster[index] = attendee;
      this.setState({ roster });
    }

    // participants props proceed
    let {participants} = this.props;
    let keys = Object.keys(newRoster);
    let parts = [];
    keys.map(key => {

      if(key !== this.props.joinInfo.Attendee.AttendeeId) {
        let isExisting = false;
        participants.map(participant => {
          if(participant.attendeeId === key) {
            parts.push({
              ...participant,
              mute: newRoster[key].muted,
              name: newRoster[key].name,
            });
            isExisting = true;
          }
        });
        
        if(!isExisting) {
          let item = {
            name: newRoster[key].name,
            mute: newRoster[key].muted,
            host: false,
            raisehand: false,
            attendeeId: key
          }
    
          parts.push(item);
        }
      }
    });
    
    this.props.setParts(parts);

  };

  videoTileDidUpdateCallback = async (tileState) => {
    
    if (
      tileState.boundAttendeeId &&
      // tileState.localTile &&
      tileState.isContent &&
      tileState.tileId
    ) {
      if(tileState.boundAttendeeId === this.props.joinInfo.Attendee.AttendeeId + '#content') {
        this.setState({isMyContent: true});
      } else {
        this.setState({isMyContent: false});
      }

      const shareVideoElement = document.getElementById(`share_video`);
      this.props.chime.audioVideo.bindVideoElement(
        tileState.tileId,
        shareVideoElement
      );

      this.setState({isSharingContent: true, sharingTileId: tileState.tileId});
      this.setState({
        sharingInfo: this.previousRoster[tileState.boundAttendeeId.replace('#content', '')]
      });
      return;
    } else {
      this.setState({isSharingContent: false});
    }

    if (
      !tileState.boundAttendeeId ||
      tileState.localTile ||
      tileState.isContent ||
      !tileState.tileId
    ) {
      return;
    }

    let index = this.findRosterSlot(tileState.boundAttendeeId);
    const roster = this.state.roster;
    const attendee = {
      ...roster[index],
      videoEnabled: tileState.active,
      attendeeId: tileState.boundAttendeeId,
      tileId: tileState.tileId
    };
    roster[index] = attendee;
    this.setState({ roster });

    setTimeout(() => {
      if (config.DEBUG) console.log(roster[index]);
      const videoElement = document.getElementById(`video_${tileState.boundAttendeeId}`);
      if (videoElement) {
        this.props.chime.audioVideo.bindVideoElement(
          tileState.tileId,
          videoElement
        );
      }
    }, 1000);
  };

  videoTileWasRemovedCallback = (tileId) => {
    let roster = this.state.roster;

    // Find the removed tileId in the roster and mark the video as disabled.
    // eslint-disable-next-line
    if(tileId === this.state.sharingTileId) {
      this.setState({
        sharingTileId: 0, 
        isMyContent: false,
        isSharingContent: false,
        sharingInfo: {}
      });
    }

    roster.find((o, i) => {
      if (o.tileId === tileId) {
        roster[i].videoEnabled = false;
        this.setState({ roster });
        if (config.DEBUG) console.log(`Tile was removed ${tileId}`);
      }
    });
  }

  componentDidMount() {
    const roster = [];
    // eslint-disable-next-line
    Array.from(Array(MAX_REMOTE_VIDEOS).keys()).map((key, index) => {
      roster[index] = {
        videoElement: React.createRef()
      };
    });
    this.setState({ roster });

    this.props.chime.subscribeToRosterUpdate(this.rosterCallback);

    this.props.chime.audioVideo.addObserver({
      videoTileDidUpdate: this.videoTileDidUpdateCallback,
      videoTileWasRemoved: this.videoTileWasRemovedCallback,
    });
  }

  componentWillUnmount() {
    this.props.chime.unsubscribeFromRosterUpdate(this.rosterCallback);
  }

  stopHandle = async () => {
    await this.props.chime.audioVideo.stopContentShare();
  }

  render() {
    let count = 0;
    this.state.roster.map((attendee) => {
      if(attendee.attendeeId) count++;
    })

    let {shareVideoElement, isMyContent, isSharingContent, sharingInfo} = this.state;
    
    return(
      <React.Fragment>
        <div className={`sharing-video-container ${isMyContent || !isSharingContent ? 'display-none': ''}`}>
          <video ref={shareVideoElement} className={`${this.props.isOpenChat ? 'opened-width-share-video' : ''}`} id='share_video' />
        </div>

        {isSharingContent && (
          <div className={'sharing-headline-container'}>
            {isMyContent ? <span className="sharing-text">You are currently sharing your screen</span> : 
            <span className="sharing-text">{sharingInfo.name} is currently screen sharing</span>}
            
            {isMyContent && <button className="mycontent-stop-sharing" onClick={this.stopHandle}>STOP SHARING</button>}
          </div>
        )}
        
        
        <div className={`remote-cams-container ${!isMyContent && isSharingContent ? 'display-none' : ''}`}>
          {this.state.roster.map((attendee, index) => {
            return (
              <RemoteVideo
                chime={this.props.chime}
                key={index}
                attendeeId={attendee.attendeeId}
                videoEnabled={attendee.videoEnabled}
                name={attendee.name}
                muted={attendee.muted}
                videoElement={attendee.videoElement}
                count={count}
                joinInfo={this.props.joinInfo}
              />
            );
          })}
        </div>
      </React.Fragment>
    )
  }
}

RemoteVideoGroup.propTypes = {
  chime: PropTypes.object,
  joinInfo: PropTypes.object,
  participants: PropTypes.array,
  setParts: PropTypes.func,
  isOpenChat: PropTypes.bool
};

export default RemoteVideoGroup;
