import React, { Component } from "react";
import { withRouter } from "react-router-dom";

// Styles
import "./Alert.css";
import logo from "./../../assets/Logo.svg";

class AlertRemoved extends Component {

    render() {
        return(
        <div className={"alert-container"}>
            <div className={"alert-back-drop"}>
            </div>
            <div className={"alert-content"}>
                <img className="alert-logo" src={logo} alt="Logo" />
                <p>Sorry, you have been removed from the session by the host.</p>
                <p>Unfortunately, you cannot rejoin.</p>
            </div>
        </div>
        );
        
    }
}

export default withRouter(AlertRemoved);