import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import * as config from "../../config";
import {
    DefaultPromisedWebSocketFactory,
    DefaultDOMWebSocketFactory,
    FullJitterBackoff,
    ReconnectingPromisedWebSocket,
    MeetingSessionStatusCode
  } from 'amazon-chime-sdk-js';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';

// Components
// import VideoPlayer from "../videoPlayer/VideoPlayer";
import Chat from "../chat/Chat";
import Controls from "./Controls";
import Settings from "./Settings";
import Error from "./Error";
import LocalVideo from './LocalVideo';
import RemoteVideoGroup from './RemoteVideoGroup';
import Alert from './Alert';
import AlertWaiting from './AlertWaiting';

// Styles
import "./ChimeWeb.css";
import "./Meeting.css";

import logo from "./../../assets/Logo.svg";
import bodylogo from "../../assets/UoL.svg";
import vicon from "../../assets/svgs/v-icon.svg";

class Meeting extends Component {
    constructor() {
        super();

        this.baseHref = config.BASE_HREF;
        this.ssName = "";

        this.audioElementRef = React.createRef();
        this.myVideoElement = React.createRef();
        this.WEB_SOCKET_TIMEOUT_MS = 130000;
    }

    state = {
        meetingStatus: null, // Loading, Success or Failed
        showSettings: false,
        showError: false,
        errorMsg: "",
        showPopupMessage: false,
        isChat: true,
        isAvailable: false,

        messages: [],
        connection: null,
        participants: [],
        handRaiseStatus: true,
        muted: false,

        isLive: false,
    };

    showModal = () => {
        this.setState({ showPopupMessage: true });
    };

    openChat = () => {
        this.setState({ showPopupMessage: true, isChat: true });
    }

    openPT = () => {
        this.setState({ showPopupMessage: true, isChat: false });
    }

    changeChat = (index) => {
        if(index === 0) this.setState({ isChat: true });
        if(index === 1) this.setState({ isChat: false });
    }

    hideModal = () => {
        this.setState({ showPopupMessage: false });
    };

    componentDidMount() {
        const start = async () => {
            try {
                const qs = new URLSearchParams(this.props.location.search);
                const room = qs.get("room");
                this.ssName = `chime[${room}]`;
                if (!room || !sessionStorage.getItem(this.ssName)) {
                    this.props.history.push(`${this.baseHref}/`);
                }

                const ssData = JSON.parse(sessionStorage.getItem(this.ssName));
                if (config.DEBUG) console.log(ssData);

                this.username = ssData.username;
                this.title = ssData.title;
                this.role = ssData.role;

                if (!ssData.joinInfo) {
                    this.joinInfo = await this.props.chime.createRoom(
                        this.role,
                        this.username,
                        this.title,
                        ssData.playbackURL
                    );
                    const data = {
                        ...ssData,
                        joinInfo: this.joinInfo,
                    };
                    sessionStorage.setItem(this.ssName, JSON.stringify(data));
                    this.playbackURL = this.joinInfo.PlaybackURL;
                } else {
                    // Browser refresh
                    this.joinInfo = ssData.joinInfo;
                    this.playbackURL = ssData.joinInfo.PlaybackURL;
                    await this.props.chime.reInitializeMeetingSession(
                        this.joinInfo,
                        this.username
                    );
                }

                this.setState({ meetingStatus: "Success" });

                this.props.chime.audioVideo.addObserver({
                    audioVideoDidStop: async (sessionStatus) => {
                        if (
                            sessionStatus.statusCode() ===
                            MeetingSessionStatusCode.AudioCallEnded
                        ) {
                            const whereTo = `${this.baseHref}/${
                                this.role === "host" ? "" : "end"
                            }`;
                            this.props.chime.leaveRoom(this.role === "host");
                            this.props.history.push(whereTo);
                        }
                    },
                });

                // this.props.chime.joinRoom(this.audioElementRef.current);

                // await this.props.chime.joinRoom(this.audioElementRef.current);

                // inverk init websocket connection
                this.initChatConnection();
            } catch (error) {
                // eslint-disable-next-line
                console.error(error);
                this.setState({ meetingStatus: "Failed" });
            }
        };
        start();

        try{
            if(!navigator.mediaDevices.enumerateDevices) {
              this.setState({isAvailable: true});
            }
          } catch(e) {
            console.log(e)
            this.setState({isAvailable: true});
          }

        //   const isChrome = navigator.userAgent.indexOf("Chrome") !== -1;
        //   const isChrome = /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor);
          const isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
          
          if(isIOS) {
            const isChrome = navigator.userAgent.indexOf("CriOS") !== -1;
            if(isChrome) {
              this.setState({isAvailable: true});
            }
          }
    }

    // init connection
    
  async initChatConnection() {
    const { Meeting, Attendee } = this.joinInfo;
    const messagingUrl = `${config.CHAT_WEBSOCKET}?MeetingId=${Meeting.MeetingId}&AttendeeId=${Attendee.AttendeeId}&JoinToken=${Attendee.JoinToken}`
    const connection = new ReconnectingPromisedWebSocket(
      messagingUrl,
      [],
      'arraybuffer',
      new DefaultPromisedWebSocketFactory(new DefaultDOMWebSocketFactory()),
      new FullJitterBackoff(1000, 0, 10000)
    );

    if (config.DEBUG) console.log(connection);

    await connection.open(this.WEB_SOCKET_TIMEOUT_MS);

    connection.addEventListener('message', async event => {
      let {messages, participants} = this.state;
      const messageData = JSON.parse(event.data);
      
      if(messageData.Type === 'sendmessage') {
        messages.push({
          id: messageData.MessageId,
          timestamp: messageData.Time,
          username: messageData.Sender,
          message: messageData.Content,
          vote: messageData.Vote,
        });

        this.setState({ messages });
      } else if(messageData.Type === 'upvote') {
        messages.map(message => {
          if(message.id === messageData.MessageId) {
            message.vote = messageData.Vote
          }
        });

        this.setState({messages});
      } else if(messageData.Type === 'deletemessage') {
        let tempMessages = [];
        messages.map(message => {
          if(message.id !== messageData.MessageId) {
            tempMessages.push(message);
          }
        });
        
        this.setState({messages: tempMessages});
      } else if(messageData.Type === 'getall') {
        let tempMessages = [];
        messageData.messages.map(message => {
          tempMessages.push({
            id: message.MessageId,
            timestamp: message.Time,
            username: message.Sender,
            message: message.Content,
            vote: message.Vote,
          })
        });
        
        this.setState({messages: tempMessages});
      } else if(messageData.Type === 'raisehand') {
        participants.map(participant => {
          if(participant.attendeeId === messageData.Sender) {
            participant.raisehand = messageData.Data === 'false' ? true : false;
            this.setErrorMsg(`${participant.name} has raised their hand.`)
          }
        });
        this.setState({participants});

        if(messageData.Sender === this.joinInfo.Attendee.AttendeeId) {
            this.setState({handRaiseStatus: messageData.Data === 'false' ? false: true})
        }
      } else if(messageData.Type === 'downhand') {
        participants.map(participant => {
            if(participant.attendeeId === messageData.Data) {
              participant.raisehand = false;
            }
          });
          this.setState({participants});
  
          if(messageData.Data === this.joinInfo.Attendee.AttendeeId) {
            this.setState({handRaiseStatus: true});
            this.setErrorMsg("Your hand has been lowered.");
          }
      } else if(messageData.Type === 'muteremote') {
        if(this.joinInfo.Attendee.AttendeeId === messageData.Data) {
            this.props.chime.audioVideo.realtimeMuteLocalAudio();
            this.setErrorMsg('You have been muted by the host.');
        }
      } else if(messageData.Type === 'removeremote') {
        if(this.joinInfo.Attendee.AttendeeId === messageData.Data) {
            this.setErrorMsg('Host removed you!')
            await this.props.chime.leaveRoom(this.role === 'host');
            sessionStorage.removeItem(this.ssName);
            const whereTo = `${this.baseHref}/${this.role === 'host' ? '' : 'removed'}`;
            this.props.history.push(whereTo);
        }
      } else if(messageData.Type === 'makehost') {
        if(this.joinInfo.Attendee.AttendeeId === messageData.Data) {
            this.role = 'host';
            const ssData = JSON.parse(sessionStorage.getItem(this.ssName));
            ssData.role = this.role;
            sessionStorage.setItem(this.ssName, JSON.stringify(ssData));
            this.setErrorMsg('You are now a host!');
        }
      } else if(messageData.Type === 'gotolive') {
        this.setState({isLive: true});
        
        await this.props.chime.joinRoom(this.audioElementRef.current);
        if(messageData.Host === this.joinInfo.Attendee.AttendeeId) {
            this.setErrorMsg('The session s now live!');
        } else {
            this.setErrorMsg('The session s now live!');
        }
      } else if(messageData.Type === 'getlivestatus') {
        this.setState({isLive: messageData.status});
        if(messageData.status) {
            await this.props.chime.joinRoom(this.audioElementRef.current);
        }
      } else if(messageData.Type === 'beforepart') {
        if(!this.state.isLive && this.role === 'host') {
            let {participants} = this.state;
            let isExisting = false;
            participants.map(part => {
                if(part.attendeeId === messageData.Sender) isExisting = true;
            })
            if(!isExisting) {
                participants.push({
                    name: messageData.Name,
                    mute: false,
                    host: false,
                    raisehand: false,
                    attendeeId: messageData.Sender
                });
            }

            this.setParts(participants);
        }
        
      }
      
    });

    let data = `{
      "message": "sendmessage",
      "type": "getall"
    }`;
    connection.send(data);

    data = `{
        "message": "sendmessage",
        "title": "${this.title}",
        "type": "getlivestatus"
    }`;
    connection.send(data);

    if(!this.state.isLive && this.role !== 'host') {
        data = `{
            "message": "sendmessage",
            "name": "${this.username}",
            "type": "beforepart"
        }`;
        connection.send(data);
    }

    this.setState({ connection });

    // this.chatRef.current.focus();
  }

    /*
     * Settings
     */

    openSettings = () => {
        this.setState({ showSettings: true });
    };

    closeSettings = () => {
        this.setState({ showSettings: false });
    };

    handleClick = (e) => {
        const { showSettings } = this.state;
        if (showSettings) {
            let node = e.target;
            let isModal = false;
            while (node) {
                if (
                    node &&
                    node.classList &&
                    node.classList.contains("modal__el")
                ) {
                    isModal = true;
                    break;
                }
                node = node.parentNode;
            }
            if (!isModal) {
                this.setState({ showSettings: false });
            }
        }
    };

    saveSettings = (
        playbackURL,
        currentAudioInputDevice,
        currentAudioOutputDevice,
        currentVideoInputDevice
    ) => {
        this.setState({
            showSettings: false,
            currentAudioInputDevice,
            currentAudioOutputDevice,
            currentVideoInputDevice,
        });
    };

    setMetadataId = (metadataId) => {
        this.setState({ metadataId });
    };

    setErrorMsg = (errorMsg) => {
        this.setState({ errorMsg, showError: true });
    };

    closeError = () => {
        this.setState({ showError: false });
    };

    endButtonOnClick = async () => {
        confirmAlert({
            title: 'Confirm to leave',
            message: 'Are you sure you want to leave?',
            buttons: [
              {
                label: 'Yes',
                onClick: ()=> this.leaveroom()
              },
              {
                label: 'No',
                onClick: () => console.log()
              }
            ]
        })
    }

    leaveroom = async () => {
        await this.props.chime.leaveRoom(this.role === 'host');
        sessionStorage.removeItem(this.ssName);
        const whereTo = `${this.baseHref}/${this.role === 'host' ? '' : 'join?room=' + this.title}`;
        this.props.history.push(whereTo);
    }

    handleMute = (localMuted) => {
        this.setState({ muted: localMuted });
    }

    handlGoToLive = () => {
        const data = `{
          "message": "sendmessage",
          "title": "${this.title}",
          "type": "gotolive"
        }`;
        this.state.connection.send(data);
      }

    layout = () => {
        if (this.state.meetingStatus !== "Success") {
            return;
        }

        return (
            <div className="meeting-grid" onClick={this.handleClick}>
                <div className="meeting-header">
                    <img className="header-logo" src={logo} alt="Logo" />
                    <div className="body-logo">
                        <span> SPONSORED BY: </span>
                        <img src={bodylogo} alt="body logo"/>
                    </div>
                </div>
                <div className="main-body">
                    {this.state.showSettings && (
                        <Settings
                            chime={this.props.chime}
                            joinInfo={this.joinInfo}
                            saveSettings={this.saveSettings}
                        />
                    )}
                    <div className="main-stage body-left">
                        <div className="body-before">
                            <div className="body-wrapper">
                                
                                <LocalVideo
                                    chime={this.props.chime}
                                    joinInfo={this.joinInfo}
                                />
                                <div className="mobile-buttons">
                                    {(!this.state.isLive && this.role === 'host') &&
                                     <button className="go-livebtn" onClick={this.handlGoToLive}>
                                        <span className="txt-onbtn">GO LIVE</span>
                                    </button>}
                                   
                                    <button className="live-meetingbtn txt-onbtn" onClick={this.endButtonOnClick}>
                                        <span className="txt-onbtn">LEAVE MEETING</span>
                                    </button>
                                </div>
                                
                                <div className="remote-cams">
                                    <RemoteVideoGroup
                                        chime={this.props.chime}
                                        joinInfo={this.joinInfo}
                                        participants={this.state.participants}
                                        setParts={this.setParts}
                                        isOpenChat={this.state.showPopupMessage}
                                    />
                                </div>
                                
                            </div>
                        </div>
                        <div className="meeting-footer">
                            <Controls
                                chime={this.props.chime}
                                baseHref={this.baseHref}
                                ssName={this.ssName}
                                title={this.title}
                                openSettings={this.openSettings}
                                role={this.role}
                                history={this.props.history}
                                myVideoElement={this.myVideoElement}
                                openChat={this.openChat}
                                openPT={this.openPT}
                                closeChat={this.hideModal}
                                isOpenChat={this.state.showPopupMessage}
                                joinInfo={this.joinInfo}
                                connection={this.state.connection}
                                handRaiseStatus={this.state.handRaiseStatus}
                                muted={this.state.muted}
                                setMute={this.handleMute}
                                isLive={this.state.isLive}
                            />
                        </div>
                    </div>
                    <div className="right-sidebar">
                        {!this.state.showPopupMessage ? (
                            " "
                        ) : (
                            <button className={"btnPopdownMessage btnPopupMessageFont"} onClick={this.hideModal}>
                                <img src={vicon} alt="v right" className={'vicon vright'}/>
                            </button>
                        )}
                        {this.state.showPopupMessage ? (
                            " "
                        ) : (
                            <button className={"btnPopupMessage btnPopupMessageFont"} onClick={this.showModal}>
                                <img src={vicon} alt="v left" className={'vicon vleft'}/>
                                <span>{this.role === 'host' ? 'Q&A/PARTICIPANTS': 'Q&A'}</span>
                            </button>
                        )}
                        {!this.state.showPopupMessage ? (
                            " "
                        ) : (
                            <Chat
                                chime={this.props.chime}
                                title={this.title}
                                username={this.username}
                                joinInfo={this.joinInfo}
                                closeChat={this.hideModal}
                                isChat={this.state.isChat}
                                changeTab={this.changeChat}
                                connection={this.state.connection}
                                messages={this.state.messages}
                                participants={this.state.participants}
                                role={this.role}
                                setErrorMsg={this.setErrorMsg}
                            />
                        )}
                        
                    </div>
                </div>
            </div>
        );
    };

    setParts = (participants) => {
        this.setState({participants})
    }

    render() {
        const {isAvailable, isLive} = this.state;
        return (
            <React.Fragment>
                {isAvailable && <Alert />}
                {(this.role === 'attendee' && !isLive) && <AlertWaiting />}
                <audio ref={this.audioElementRef} style={{ display: "none" }} />

                {this.layout()}

                {this.state.showError && (
                    <Error
                        closeError={this.closeError}
                        errorMsg={this.state.errorMsg}
                    />
                )}
            </React.Fragment>
        );
    }
}

Meeting.propTypes = {
    chime: PropTypes.object,
};

export default withRouter(Meeting);
